// =============================================================================
// DEBUG API: SMF FACILITATOR RANKINGS
// =============================================================================
// Exposes x402scan metrics, Scattering data, and SMF ranking information
// GET /api/debug/smf-facilitators?network=eip155:8453&timeframe=1d

import { NextResponse } from 'next/server';
import { createLogger } from '@/lib/logger';
import {
  getAllFacilitators,
  getFacilitatorsForNetwork,
  type FacilitatorRegistryEntry,
} from '@/integrations/x402/facilitators/facilitator-registry';
import {
  rankFacilitators,
  getRecommendedFacilitator,
  getFacilitatorExplainer,
  type FacilitatorExplanation,
} from '@/services/facilitator-metrics-reader';
import { getScatteringMetricsForFacilitators, computeActivityScore } from '@/services/facilitator-volume-service';
import type { FacilitatorPathMetricsTimeframe } from '@/infra/x402scan/types';
import type { ScatteringFacilitatorMetrics } from '@/infra/scattering/types';

const logger = createLogger({ component: 'DebugSMFFacilitators' });

// =============================================================================
// TYPES
// =============================================================================

/** Activity level derived from Scattering metrics */
type ActivityLevel = 'high' | 'medium' | 'low' | 'none';

/** Scattering metrics for display */
interface ScatteringDisplayMetrics {
  volumeUsd3d: number;
  txCount3d: number;
  uniqueBuyers3d: number;
  uniqueSellers3d: number;
  volumeChangeRate?: number;
  activityLevel: ActivityLevel;
  activityScore: number;
  fetchedAt?: string;
}

/** NexFlow internal traffic metrics (placeholder) */
interface NexFlowTrafficMetrics {
  last24hInvocations: number;
  last24hDogfoodInvocations: number;
  last24hExternalInvocations: number;
  last24hSuccessRate?: number;
  last24hP95LatencyMs?: number;
}

/** Data freshness info */
interface DataFreshness {
  x402scan: {
    ageHours: number | null;
    lastUpdated: string | null;
    isStale: boolean;
  };
  scattering: {
    ageHours: number | null;
    lastUpdated: string | null;
    isStale: boolean;
  };
  nexflow: {
    ageHours: number | null;
    lastUpdated: string | null;
    isStale: boolean;
  };
}

interface FacilitatorDebugInfo {
  id: string;
  name: string;
  baseUrl: string;
  networks: string[];
  score: number;
  rank: number;
  selected: boolean;
  x402scanScore?: number;
  confidence?: 'high' | 'medium' | 'low' | 'none';
  shortReason?: string;
  metrics?: {
    successRate: number;
    errorRate: number;
    p95LatencyMs?: number;
    invocations: number;
    dataAgeHours: number;
  };
  scattering?: ScatteringDisplayMetrics;
  nexflowTraffic?: NexFlowTrafficMetrics;
  reasons?: string[];
  externalLinks?: {
    x402scan?: string;
    scattering?: string;
  };
}

interface DebugResponse {
  ok: true;
  network: string | null;
  timeframe: FacilitatorPathMetricsTimeframe;
  usedFallback: boolean;
  recommended: string | null;
  recommendationReason: string;
  facilitators: FacilitatorDebugInfo[];
  dataFreshness: DataFreshness;
  timestamp: string;
}

interface ErrorResponse {
  ok: false;
  error: string;
}

// =============================================================================
// HELPERS
// =============================================================================

/** Determine activity level from Scattering metrics */
function getActivityLevel(metrics: ScatteringFacilitatorMetrics | null): ActivityLevel {
  if (!metrics) return 'none';
  
  // Based on 3d metrics
  if (metrics.volumeUsd3d >= 10000 || metrics.txCount3d >= 100000) return 'high';
  if (metrics.volumeUsd3d >= 1000 || metrics.txCount3d >= 10000) return 'medium';
  if (metrics.volumeUsd3d > 0 || metrics.txCount3d > 0) return 'low';
  return 'none';
}

/** Format Scattering metrics for display */
function formatScatteringMetrics(metrics: ScatteringFacilitatorMetrics | null): ScatteringDisplayMetrics | undefined {
  if (!metrics) return undefined;
  
  return {
    volumeUsd3d: metrics.volumeUsd3d,
    txCount3d: metrics.txCount3d,
    uniqueBuyers3d: metrics.uniqueBuyers3d,
    uniqueSellers3d: metrics.uniqueSellers3d,
    volumeChangeRate: metrics.volumeChangeRate,
    activityLevel: getActivityLevel(metrics),
    activityScore: Math.round(computeActivityScore(metrics) * 100),
    fetchedAt: metrics.fetchedAt,
  };
}

/** Calculate hours since timestamp */
function hoursSince(timestamp: string | undefined): number | null {
  if (!timestamp) return null;
  const then = new Date(timestamp).getTime();
  const now = Date.now();
  return Math.round((now - then) / (1000 * 60 * 60) * 10) / 10;
}

/** Get external links for facilitator */
function getExternalLinks(facilitatorId: string): { x402scan?: string; scattering?: string } {
  const links: { x402scan?: string; scattering?: string } = {};
  
  // x402scan facilitator page
  const x402scanMap: Record<string, string> = {
    'cdp': 'coinbase',
    'payai': 'payai',
    'dexter': 'dexter',
    'x402rs': 'x402rs',
  };
  if (x402scanMap[facilitatorId]) {
    links.x402scan = `https://www.x402scan.com/facilitator/${x402scanMap[facilitatorId]}`;
  }
  
  // Scattering page (uses project slug)
  links.scattering = `https://scattering.io/x402`;
  
  return links;
}

// =============================================================================
// ROUTE HANDLER
// =============================================================================

export async function GET(request: Request): Promise<NextResponse<DebugResponse | ErrorResponse>> {
  try {
    const { searchParams } = new URL(request.url);
    const network = searchParams.get('network') || null;
    const timeframe = (searchParams.get('timeframe') || '1d') as FacilitatorPathMetricsTimeframe;

    // Validate timeframe
    if (!['1d', '7d', '30d'].includes(timeframe)) {
      return NextResponse.json(
        { ok: false, error: `Invalid timeframe: ${timeframe}. Must be 1d, 7d, or 30d.` },
        { status: 400 }
      );
    }

    // Get facilitators (filtered by network if provided)
    let facilitators: FacilitatorRegistryEntry[];
    if (network) {
      facilitators = getFacilitatorsForNetwork(network);
      if (facilitators.length === 0) {
        return NextResponse.json({
          ok: true,
          network,
          timeframe,
          usedFallback: true,
          recommended: null,
          recommendationReason: `No facilitators support network: ${network}`,
          facilitators: [],
          dataFreshness: {
            x402scan: { ageHours: null, lastUpdated: null, isStale: true },
            scattering: { ageHours: null, lastUpdated: null, isStale: true },
            nexflow: { ageHours: null, lastUpdated: null, isStale: true },
          },
          timestamp: new Date().toISOString(),
        });
      }
    } else {
      facilitators = getAllFacilitators();
    }

    const facilitatorIds = facilitators.map((f) => f.id);

    // Fetch Scattering metrics for all facilitators
    const scatteringMetrics = await getScatteringMetricsForFacilitators(facilitatorIds);

    // Get recommendation and rankings
    const recommendation = await getRecommendedFacilitator(facilitatorIds, {
      network: network ?? undefined,
      timeframe,
      prioritize: 'balanced',
    });

    // Track data freshness
    let oldestX402scanAge: number | null = null;
    let oldestX402scanUpdated: string | null = null;
    let oldestScatteringAge: number | null = null;
    let oldestScatteringUpdated: string | null = null;

    // Get detailed explanations for each facilitator
    const facilitatorDebugInfo: FacilitatorDebugInfo[] = [];

    for (let i = 0; i < recommendation.ranking.rankings.length; i++) {
      const ranking = recommendation.ranking.rankings[i];
      const registryEntry = facilitators.find((f) => f.id === ranking.facilitatorId);
      const scattering = scatteringMetrics.get(ranking.facilitatorId) ?? null;
      
      // Get detailed explanation
      let explanation: FacilitatorExplanation | null = null;
      try {
        explanation = await getFacilitatorExplainer(ranking.facilitatorId, {
          timeframe,
          wasSelected: ranking.facilitatorId === recommendation.recommended,
        });
      } catch (error) {
        logger.debug({ error, facilitatorId: ranking.facilitatorId }, 'Failed to get explanation');
      }

      // Track x402scan freshness
      if (explanation?.metrics?.dataAgeHours !== undefined) {
        if (oldestX402scanAge === null || explanation.metrics.dataAgeHours > oldestX402scanAge) {
          oldestX402scanAge = explanation.metrics.dataAgeHours;
          // Approximate lastUpdated from dataAgeHours
          oldestX402scanUpdated = new Date(Date.now() - explanation.metrics.dataAgeHours * 60 * 60 * 1000).toISOString();
        }
      }

      // Track Scattering freshness
      if (scattering?.fetchedAt) {
        const scatteringAge = hoursSince(scattering.fetchedAt);
        if (scatteringAge !== null && (oldestScatteringAge === null || scatteringAge > oldestScatteringAge)) {
          oldestScatteringAge = scatteringAge;
          oldestScatteringUpdated = scattering.fetchedAt;
        }
      }

      // Build enriched short reason with Scattering data for non-CDP facilitators
      let enrichedShortReason = explanation?.shortReason;
      if (scattering && !explanation?.shortReason?.includes('success')) {
        // Add Scattering context for facilitators without x402scan data
        const activityLabel = getActivityLevel(scattering);
        enrichedShortReason = `${ranking.facilitatorId}: ${activityLabel} activity, $${formatVolume(scattering.volumeUsd3d)} 3d vol, ${formatCount(scattering.txCount3d)} txns`;
      }

      const debugInfo: FacilitatorDebugInfo = {
        id: ranking.facilitatorId,
        name: registryEntry?.name ?? ranking.facilitatorId,
        baseUrl: registryEntry?.baseUrl ?? 'unknown',
        networks: registryEntry?.networks ?? [],
        score: Math.round(ranking.score * 10) / 10,
        rank: i + 1,
        selected: ranking.facilitatorId === recommendation.recommended,
        x402scanScore: explanation?.score ?? ranking.score,
        confidence: ranking.confidence,
        shortReason: enrichedShortReason,
        metrics: explanation?.metrics ? {
          successRate: explanation.metrics.successRate,
          errorRate: explanation.metrics.errorRate,
          p95LatencyMs: explanation.metrics.p95LatencyMs,
          invocations: explanation.metrics.invocations,
          dataAgeHours: Math.round(explanation.metrics.dataAgeHours * 10) / 10,
        } : undefined,
        scattering: formatScatteringMetrics(scattering),
        nexflowTraffic: {
          // Placeholder - will be populated once we have internal traffic logging
          last24hInvocations: 0,
          last24hDogfoodInvocations: 0,
          last24hExternalInvocations: 0,
        },
        reasons: ranking.reasons,
        externalLinks: getExternalLinks(ranking.facilitatorId),
      };

      facilitatorDebugInfo.push(debugInfo);
    }

    // Add facilitators with no ranking data
    for (const facilitator of facilitators) {
      if (!facilitatorDebugInfo.some((f) => f.id === facilitator.id)) {
        const scattering = scatteringMetrics.get(facilitator.id) ?? null;
        const activityLabel = getActivityLevel(scattering);
        
        let shortReason = 'No x402scan data available';
        if (scattering) {
          shortReason = `${facilitator.id}: ${activityLabel} activity, $${formatVolume(scattering.volumeUsd3d)} 3d vol, ${formatCount(scattering.txCount3d)} txns`;
        }

        facilitatorDebugInfo.push({
          id: facilitator.id,
          name: facilitator.name,
          baseUrl: facilitator.baseUrl,
          networks: facilitator.networks,
          score: 0,
          rank: facilitatorDebugInfo.length + 1,
          selected: false,
          confidence: 'none',
          shortReason,
          scattering: formatScatteringMetrics(scattering),
          nexflowTraffic: {
            last24hInvocations: 0,
            last24hDogfoodInvocations: 0,
            last24hExternalInvocations: 0,
          },
          reasons: ['no-x402scan-data'],
          externalLinks: getExternalLinks(facilitator.id),
        });
      }
    }

    // Build data freshness summary
    const X402SCAN_STALE_THRESHOLD_HOURS = 4;
    const SCATTERING_STALE_THRESHOLD_HOURS = 24;

    const dataFreshness: DataFreshness = {
      x402scan: {
        ageHours: oldestX402scanAge,
        lastUpdated: oldestX402scanUpdated,
        isStale: oldestX402scanAge === null || oldestX402scanAge > X402SCAN_STALE_THRESHOLD_HOURS,
      },
      scattering: {
        ageHours: oldestScatteringAge,
        lastUpdated: oldestScatteringUpdated,
        isStale: oldestScatteringAge === null || oldestScatteringAge > SCATTERING_STALE_THRESHOLD_HOURS,
      },
      nexflow: {
        // Placeholder - will be populated once we have internal traffic
        ageHours: null,
        lastUpdated: null,
        isStale: true,
      },
    };

    // Log the debug request
    logger.info({
      network,
      timeframe,
      usedFallback: recommendation.usedFallback,
      recommended: recommendation.recommended,
      facilitatorCount: facilitatorDebugInfo.length,
      x402scanStale: dataFreshness.x402scan.isStale,
      scatteringStale: dataFreshness.scattering.isStale,
      topFacilitator: facilitatorDebugInfo[0] ? {
        id: facilitatorDebugInfo[0].id,
        score: facilitatorDebugInfo[0].score,
        confidence: facilitatorDebugInfo[0].confidence,
      } : null,
      msg: 'SMF facilitator debug request',
    });

    const response: DebugResponse = {
      ok: true,
      network,
      timeframe,
      usedFallback: recommendation.usedFallback,
      recommended: recommendation.recommended,
      recommendationReason: recommendation.reason,
      facilitators: facilitatorDebugInfo,
      dataFreshness,
      timestamp: new Date().toISOString(),
    };

    return NextResponse.json(response);
  } catch (error) {
    const errorMessage = error instanceof Error ? error.message : 'Unknown error';
    
    logger.error({
      error: errorMessage,
      stack: error instanceof Error ? error.stack : undefined,
      msg: 'SMF facilitator debug route error',
    });

    return NextResponse.json(
      { ok: false, error: errorMessage },
      { status: 500 }
    );
  }
}

// =============================================================================
// FORMATTING HELPERS
// =============================================================================

/** Format volume for display (e.g., 12500 -> "$12.5K") */
function formatVolume(volume: number): string {
  if (volume >= 1_000_000) return `${(volume / 1_000_000).toFixed(1)}M`;
  if (volume >= 1_000) return `${(volume / 1_000).toFixed(1)}K`;
  return volume.toFixed(0);
}

/** Format count for display (e.g., 125000 -> "125K") */
function formatCount(count: number): string {
  if (count >= 1_000_000) return `${(count / 1_000_000).toFixed(1)}M`;
  if (count >= 1_000) return `${(count / 1_000).toFixed(0)}K`;
  return count.toString();
}

// =============================================================================
// OPTIONS (CORS)
// =============================================================================

export async function OPTIONS(): Promise<NextResponse> {
  return new NextResponse(null, {
    status: 204,
    headers: {
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'GET, OPTIONS',
      'Access-Control-Allow-Headers': 'Content-Type',
    },
  });
}

